import { getSingleOrder } from "@/api";
import { SingleOrderLeft, SingleOrderRight } from "@/components/single-order";
import { getCookie } from "cookies-next";
type Props = {
  params: Promise<{ id: string }>;
};
export default async function SingleOrderPage({ params }: Props) {
  const token = getCookie("sector-token");
  console.log("token", token);

  const { id } = await params;
  const order = await getSingleOrder(id, token);
  console.log("order", order);

  return (
    <section className="grid grid-cols-12 gap-6">
      {/* single order left */}
      <SingleOrderLeft orderSort={order} />
      {/* single order right */}
      <SingleOrderRight />
    </section>
  );
}
